.header{
    &__right{
        display: flex;
        align-items: center;
    }
}

.logOut__button{
    padding: 0.3em 1em;
    margin-right: 3em;
    border: 1px solid rgba(255, 255, 255, 0.411);
    background: linear-gradient(45deg, rgba(0, 0, 255, 0.616), rgba(210, 213, 236, 0.336), purple);
    color: white;
    box-shadow: inset 0px 0px 3px rgb(184, 172, 192);
    cursor: pointer;
}
.theme__toggle{
    width: 80px;
    height: 32px;
    margin: 10px 10em 10px 0 ;
    padding: 0px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    border: 2px solid gray;
    &:before{
        content: '';
        width: 30px;
        height: 29px;
        background-color: white;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &.light{
        &:before{
            transform: translateX(2%);
            transform: translateY(-2%);
            background-color: black;
            transition-duration: 1s;
        }
        background-color: white;
    }
    &.dark{
        &:before{
            transform: translateX(153%);
            height: 28px;
            background-color: white;
            transition-duration: 1s;
        }
        background-color: rgba(0, 0, 0, 0.897);
    }
}

@media screen and (max-width: 768px){
    .theme__toggle{
        margin-right: 2.5em;
    }
}